import React, { useState } from 'react';
import WheelOfPrizes from 'react-wheel-of-prizes';
import styled from 'styled-components';

const cashPrizes = [
  { title: "\u20A8 100" },
  { title: "\u20A8 500" },
  { title: "\u20A8 300" },
  { title: "\u20A8 50" }
];

const SpinnerWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f0f0;
`;

const PrizeText = styled.h2`
  margin-top: 20px;
  font-size: 2rem;
`;

const Spinner = () => {
  const [prize, setPrize] = useState(null);
  const [spinning, setSpinning] = useState(false);

  const handleSpin = () => {
    setSpinning(true);
  };

  const handleFinish = (winningItem) => {
    setPrize(winningItem.title);
    setSpinning(false);
  };

  return (
    <SpinnerWrapper>
      <WheelOfPrizes
        segments={cashPrizes}
        onFinish={handleFinish}
        isSpinning={spinning}
        onClick={handleSpin}
        primaryColor="#42a5f5"
        contrastColor="#fff"
        buttonText="Spin"
      />
      {prize && <PrizeText>You won {prize}!</PrizeText>}
    </SpinnerWrapper>
  );
};

export default Spinner;
