import { useState } from "react";
import { motion } from "framer-motion";
import "../styles/globals.css";
import Head from 'next/head';
import Spinner from './spinner';
import ReactSpinningWheel from 'react-spinning-wheel';
import 'react-spinning-wheel/dist/style.css';

export default function Home() {
    const [gameFinished, setGameFinished] = useState(false);
    const [score, setScore] = useState(0);  // For Score
    const [discount, setDiscount] = useState(0);  // For Discount

    const segments = [
        { option: 'Prize 1', id: 'prize1' },
        { option: 'Prize 2', id: 'prize2' },
        { option: 'Prize 3', id: 'prize3' },
        { option: 'Prize 4', id: 'prize4' },
        { option: 'Prize 5', id: 'prize5' }
    ];

    const [spinning, setSpinning] = useState(false);

    // Callback when game finishes
    const handleGameFinish = (wonAmount) => {
        setGameFinished(true);
        setScore(prevScore => prevScore + 1);  // Increase score
        setDiscount(prevDiscount => prevDiscount + wonAmount);  // Increase discount based on the amount won
    };

    // Handle spin click
    const handleSpinClick = () => {
        setSpinning(true);
        setTimeout(() => {
            setSpinning(false);
            const wonAmount = Math.random() * 100;  // Example: Random prize amount
            handleGameFinish(wonAmount);
        }, 3000); // Simulate spinning time
    };

    return (
        <div
            className="min-h-screen flex flex-col justify-center items-center text-center p-8 bg-cover bg-center"
            style={{
                backgroundImage: "url('/logo.jpg')",
                filter: "brightness(0.7) contrast(1.2)", // Light contrast effect on background
            }}
        >
            <motion.div
                className="container mx-auto max-w-3xl bg-white bg-opacity-80 p-8 rounded-xl shadow-2xl perspective-1000"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 1 }}
                style={{
                    transformStyle: "preserve-3d",
                }}
            >
                {/* Show Spinner first, if the game is not finished */}
                {!gameFinished ? (
                    <div>
                        <ReactSpinningWheel
                            segments={segments}
                            spin={spinning}
                            onComplete={() => console.log('Spin complete!')}
                        />
                        <button onClick={handleSpinClick}>Spin</button>
                    </div>
                ) : (
                    <>
                        {/* Enhanced Title */}
                        <h1 className="text-5xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-pink-500 via-purple-600 to-indigo-700 mb-8 animate-pulse">
                            Cash For Old Silk Products
                        </h1>

                        {/* Descriptive Paragraph */}
                        <motion.p
                            className="text-lg text-gray-700 mb-8 font-serif tracking-wide leading-relaxed shadow-lg"
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ duration: 1.2 }}
                        >
                            Got sarees that are sitting unused in your wardrobe? Let them find a
                            new home and earn<span className="font-bold"> extra cash </span>in the process. Join thousands of sellers
                            today! We buy <span className="font-bold">Pattu silk sarees</span> and other various products from across
                            India, making it convenient for you to sell from your city. Our top
                            priority is customer satisfaction, and we are dedicated to offering
                            exceptional service.
                        </motion.p>

                        {/* Game Score & Discount */}
                        <div className="mt-8">
                            <h3 className="text-2xl font-semibold">Your Score: {score}</h3>
                            <h3 className="text-2xl font-semibold">Your Discount: ₹{discount.toFixed(2)}</h3>
                        </div>
                    </>
                )}
            </motion.div>

            {/* Footer */}
            <footer className="mt-12 text-gray-600 text-sm">
                <p>© 2025 SareeShop. All rights reserved.</p>
            </footer>
        </div>
    );
}
